<template>
<div class="">
  <form  @submit.prevent="addTodo">
    <input type="text" placeholder="请添加待办事项..." v-model="title">
    <input type="submit" value="添加" class="btn">
  </form>
</div>
</template>
<script>
const uuid = require('uuid')
export default {
name:'AddTodo',
methods:{
  addTodo(){
    const newTodo ={
      id:uuid.v4(),
      title:this.title,
      completed:false
    }
    console.log(uuid.v4());
    this.$emit('headleAdd',newTodo)
    this.title=''
  }
},
data() {
return {
  title:''
};
}};
</script>
<style scoped>
form {
  display: flex;
}

input[type="text"] {
  flex: 10;
  padding: 5px;
}

input[type="submit"] {
  flex: 2;
}
</style>